<template>
  <div class="common-layout">
    <el-container>
      <el-header style="background-color: #a8abb2"></el-header>
      <el-main>
        <el-card style="margin-left:600px;margin-top:100px;width: 600px">
          <el-form label-width="auto" :label-position="right" ref="form" :rules="rules" :model="registerInfo">
            <el-form-item label="id" prop="id">
              <el-input size="35" v-model="registerInfo.id" placeholder="请输入id"/>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input size="35" style="margin-top: 20px" v-model="registerInfo.password" type="password" placeholder="请输入密码" show-password/>
            </el-form-item>
            <el-form-item label="姓名" prop="name">
              <el-input size="35" style="margin-top: 20px" v-model="registerInfo.name" placeholder="请输入真实姓名"/>
            </el-form-item>
            <el-form-item label="用户名" prop="username">
                <el-input size="35" style="margin-top: 20px" v-model="registerInfo.username" placeholder="请输入用户名"/>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="goToLogin">前往登陆</el-button>
              <el-button type="primary" @click="register">注册</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  name: "LoginView",
  el: "#root",
  data() {
    return {
      registerInfo: {
        id: undefined,
        password: undefined,
        name: undefined,
        username: undefined,
      },
      rules: {
        id: [
          { required: true, message: '不能为空', trigger: 'blur' },
          { validator(rule, value, callback) {
                      if (Number.isInteger(Number(value)) && Number(value) > 0)
                        callback()
                      else
                        callback(new Error('id只能为正整数'))
                      },trigger:"blur"}
        ],
        name: [{ required: true, message: '不能为空', trigger: 'blur' },],
        password: [{ required: true, message: '不能为空', trigger: 'blur' },],
        username:[{ required: true, message: '不能为空', trigger: 'blur' }],
      },
    };
  },
  methods: {
    register() {
        this.$refs.form.validate(valid=>{
            if(valid){
                axios.post("https://localhost/student/register",qs.stringify(this.registerInfo)).then((res) => {
                    if (res.status == 200) 
                      this.$message({
                        type:"success",
                        message:"注册成功，请前往登陆页面！"});
                    else if (res.status == 201)
                        this.$message({
                          type:"error",
                          message:"id已被占用"});
                });
            }
            else
                this.$message({
                          type:"error",
                          message:"输入不合法"});
        }) 
    },
    goToLogin() {
      this.$router.push("/");
    },
  },
};
</script>

