<template>
  <div class="common-layout">
    <el-container>
      <el-header style="background-color: #a8abb2"></el-header>
      <el-main>
        <el-card style="margin-left:715px;margin-top:200px;width: 500px">
          <el-form label-width="auto" :label-position="right" ref="form" :rules="rules" :model="loginInfo">
            <el-form-item label="用户id" prop="id">
              <el-input size="35" placeholder="请输入id" v-model="loginInfo.id"/>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input size="35"  placeholder="请输入密码" v-model="loginInfo.password" type="password" @keyup.enter="login" show-password/>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" round @click="login">登陆</el-button>
              <el-button type="primary" round @click="goToRegister">注册</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  name: "LoginView",
  el: "#root",
  data() {
    return {
      loginInfo: {
        id: undefined,
        password: undefined,
      },
      rules: {
        id: [{ required: true, message: "不能为空", trigger: "blur" }],
        password: [{ required: true, message: "不能为空", trigger: "blur" }],
      },
    };
  },
  methods: {
    login() {
          this.$refs.form.validate((valid) => {
            if (valid) {
                axios.post("https://localhost/userLogin",qs.stringify(this.loginInfo)).then((res) => {
                    if (res.status == 200) {
                      let token = {
                        id: res.data.id,
                        token: res.data.token,
                        starttime: res.data.starttime,
                      };
                      localStorage.setItem("token", JSON.stringify(token));
                      let url = res.data.url;
                      axios.post("https://localhost/user/getUser",qs.stringify(JSON.parse(localStorage.getItem('token')))).then(res=>{
                        if(res.data.permission == 2)
                          window.open(url+"/backend")
                        else if(res.data.permission == 1)
                          window.open(url+"/search")
                      })
                    }
                    else if(res.status == 201)
                      this.$message({
                        type:"error",
                        message:"用户名或密码错误"
                      })
                });
            } 
            else
               this.$message({
                        type:"error",
                        message:"输入不合法"
                      })
          });
    },
    goToRegister() {
      this.$router.push("/register");
    },
  },
};
</script>

